import { Component, OnInit, ElementRef, Renderer2, Input } from '@angular/core';
import { Router } from '@angular/router';
import { I18n_frm } from '../../models/rtf.i18n.model';
import { CacheService } from '../../../app/rtf-common';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  host: {
    'class': 'gw-header-top'
  }
})
export class HeaderComponent implements OnInit {
  @Input() hasOnApp = false;
  element: HTMLElement;
  searchText = '';
  appdialogshow = false;
  showfullscreenonapp = false;

  closeRuning = false;
  removeRuningApp = false;

  fixedSearchType = ['function', 'webform', 'gspask'];
  fixedSearchText = ['应用', '表单', '社区'];
  activeIndex = -1;

  languageCode = 'zh-CHS';
  I18n: any;
  constructor(element: ElementRef, private renderer: Renderer2, private router: Router, private cache: CacheService) {
    this.element = element.nativeElement;
  }

  ngOnInit() {
    this.I18n = I18n_frm;
    if (this.cache.get('languageCode')) {
      this.languageCode = this.cache.get('languageCode');
    }
  }
  // 向导
  navigateTo(url: string) {
    this.showfullscreenonapp = false;
    this.router.navigateByUrl(url);
  }
  classifyPanel(type: number) {
    const classifyPanel = this.element.querySelector('.topheader-search-classify') as HTMLElement;
    const defaultPanel = this.element.querySelector('.topheader-search-default-panel') as HTMLElement;
    if (type === 1) {
      this.renderer.removeClass(classifyPanel, 'd-none');
      this.renderer.addClass(defaultPanel, 'd-none');
    } else {
      this.renderer.removeClass(defaultPanel, 'd-none');
      this.renderer.addClass(classifyPanel, 'd-none');
    }
  }
  private _setIndex(type) {
    const len = this.fixedSearchType.length;
    let curIndex;
    for (let i = 0; i < len; i++) {
      if (this.fixedSearchType[i] === type) {
        curIndex = i;
        break;
      }
    }
    this.activeIndex = curIndex;
  }
  addTab(event) {
    if (!event) {
      const searchTab = this.element.querySelector('.topheader-search-tab');
      if (searchTab.querySelector('.active')) {
        this.renderer.removeClass(searchTab.querySelector('.active'), 'active');
      }
      this.activeIndex = -1;
      return;
    }
    const targetDom = event.currentTarget;
    const targetType = targetDom.getAttribute('type');
    let findDome;

    // 不存在
    if (this.fixedSearchType.join('|').indexOf(targetType + '|') < 0) {
      // 增加
      let addtext = targetDom.querySelector('.topheader-search-icon').innerHTML;
      addtext = ' <span class="topheader-search-icon">' + addtext + '</span>';
      const targetText = targetDom.querySelector('.topheader-search-text').innerText;
      addtext += '<span class="topheader-search-text">' + targetText + '</span>';
      findDome = this.element.querySelector('.topheader-search-tab').querySelector('.topheader-search-add');
      findDome.innerHTML = addtext;
      findDome.setAttribute('type', targetType);
      this.fixedSearchType.splice(2, 1, targetType);
      this.fixedSearchText.splice(2, 1, targetText);
    } else {// 已存在
      // 添加样式
      const itemDoms = this.element.querySelectorAll('.tst-item');
      let notFound = true;
      [].forEach.call(itemDoms, function (itemDom) {
        if (notFound && itemDom.getAttribute('type') === targetType) {
          notFound = false;
          findDome = itemDom;
        }
      });
    }
    this._choose(findDome);
    // this.classifyPanel(0);

  }
  choose(event) {
    this._choose(event.currentTarget);
  }
  private _choose(targetDom) {
    const searchTab = this.element.querySelector('.topheader-search-tab');
    if (searchTab.querySelector('.active')) {
      this.renderer.removeClass(searchTab.querySelector('.active'), 'active');
    }
    this.renderer.addClass(targetDom, 'active');
    const text = targetDom.querySelector('.topheader-search-text').innerText;
    const search = this.element.querySelector('#top-search') as HTMLInputElement;
    if (this.activeIndex > -1) {
      this.searchText = text + ':' + this.searchText.replace(this.fixedSearchText[this.activeIndex], '');
    } else {
      this.searchText = text + ':' + this.searchText;
    }
    this._setIndex(targetDom.getAttribute('type'));
  }
  searchChange(searchCon) {
    const panel = this.element.querySelector('.topheader-search-panel');
    if (searchCon) {
      this.renderer.addClass(panel.querySelector('.topheader-search-default-panel'), 'd-none');
      this.renderer.removeClass(panel.querySelector('.topheader-search-result'), 'd-none');
    } else {
      this.renderer.removeClass(panel.querySelector('.topheader-search-default-panel'), 'd-none');
      this.renderer.addClass(panel.querySelector('.topheader-search-result'), 'd-none');
    }
  }

  fromSearchIconClick() {
    const panel = this.element.querySelector('.fullscreen-show-search');
    this.renderer.removeClass(panel, 'd-none');
    this.renderer.removeClass(panel.querySelector('.topheader-search-default-panel'), 'd-none');
    this.renderer.addClass(panel.querySelector('.topheader-search-result'), 'd-none');
    this.addTab(null);
  }
  closeSearchPanel() {
    const panel = this.element.querySelector('.fullscreen-show-search');
    this.renderer.addClass(panel, 'd-none');
  }
  showHideOnAppDialog(show) {
    this.appdialogshow = show;
    this.removeRuningApp = false;
  }

  fromShowAllAPP() {
    this.showfullscreenonapp = true;
    this.closeRuning = false;
  }
}



