import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-layout',
  templateUrl: './layout.component.html',
  styleUrls: ['./layout.component.css']
})
export class LayoutComponent implements OnInit {
  @Input() result: any;
  @Input() funcs: any;
  hasOnAPP = false;


  constructor() { }

  ngOnInit() {
  }
  tempAppClickFunc() {
    this.hasOnAPP = true;
  }
}



