import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { FrameworkService } from '../../../app/rtf-common';
import { CacheService } from '../../../app/rtf-common';
import { FrmEventBus } from '../../../app/rtf-common';


@Component({
  selector: 'app-app-group',
  templateUrl: './app-group.component.html',
  styleUrls: ['./app-group.component.css']
})
export class AppGroupComponent implements OnInit {
  @Input() funcList: any;
  @Input() groupId: any;
  @Output() appInfoWidgetClick = new EventEmitter();

  isCollapsed = false;
  ssid: string;
  urlpath: string;

  constructor(private router: Router, private frmservice: FrameworkService, private cache: CacheService, private feb: FrmEventBus) { }

  ngOnInit() {
    this.ssid = this.cache.get('session');
  }
  handleWidgetClick(appId) {

    const path = this.funcList[appId]['InvokingConfig']['Url'];
    this.frmservice.openFunc(appId, path , this.ssid).subscribe(data => {
        this.feb.post('frmopenfunc', null);
    });
  }
}



