import {
  Component,
  OnInit,
  Input,
  OnChanges,
  SimpleChanges
} from '@angular/core';
import { DrawerItemData } from '@farris/drawer';
import { DrawerService } from '../../../../app/rtf-common';
import { Function, funcs } from '../../../models/function.model';
import { List, Map } from 'immutable';
import { normalize } from 'normalizr';
@Component({
  selector: 'app-general-drawer',
  template: `
  <app-drawer [datas]="drawerData" [customClass]="'general-drawer'" [close]="closeState" [hide]="hideState"></app-drawer>
  `,
  host: {
    class: 'd-flex'
  }
})
export class GeneralDrawerComponent implements OnInit, OnChanges {
  drawerData: DrawerItemData[];
  closeState = false;
  // 默认不显示
  hideState = true;
  constructor(private drawerSer: DrawerService) {
    this.drawerSer.getDrawerClose().subscribe({
      next: (value: boolean) => {
        this.closeState = value;
      }
    });
    this.drawerSer.getDrawerData().subscribe({
      next: (data: DrawerItemData[]) => {
        this.drawerData = data;
      }
    });
    this.drawerSer.getDrawerHide().subscribe({
      next: (value: boolean) => {
        this.hideState = value;
      }
    });
  }
  ngOnInit() {}
  ngOnChanges(changes: SimpleChanges) {}
}



