import {
  Component,
  ViewChild,
  Input,
  Output,
  EventEmitter,
  OnInit,
  ElementRef,
  Renderer2
} from '@angular/core';
// import { ModalDirective } from 'ngx-bootstrap';
import { Router } from '@angular/router';
import { I18n_frm } from '../../models/rtf.i18n.model';
import { CacheService } from '../../../app/rtf-common';
import { ModalDirective } from '../common/modal';
import { i18n_dashboard, i18n_apps } from '../../models/mock.i18n.model';

@Component({
  selector: 'app-applicationmanager',
  templateUrl: './applicationmanager.component.html',
  styleUrls: ['./applicationmanager.component.css'],
  host: {
    class: 'gw-page-header-applicationmanager'
  }
})
export class ApplicationManagerComponent implements OnInit {
  @ViewChild('staticModal') staticModal: ModalDirective;
  element: HTMLElement;
  @Input() idList: any[] = [];
  @Input() funcList = {};
  @Input() currentAppID: any;
  @Input() currentModuleID: any;
  @Output() appClick = new EventEmitter();
  @Output() moduleClick = new EventEmitter();

  @Output() tempAppClick = new EventEmitter();

  isCollapsedOn = false;
  isCollapsedCom = false;
  isCollapsedHT = false;
  isCollapsedGYL = false;

  actionType = '';
  closeRuning = false;
  appinstall = false;
  removeapp = false;

  removeappHT = false;
  removeappTB = false;
  removeappGG = false;
  removeappBX = false;

  languageCode = 'zh-CHS';
  I18n: any;
  dashboard_resource: any;
  apps_resource: any;

  constructor(
    element: ElementRef,
    private renderer: Renderer2,
    private router: Router,
    private cache: CacheService
  ) {
    this.element = element.nativeElement;
  }

  ngOnInit() {
    this.I18n = I18n_frm;
    this.dashboard_resource = i18n_dashboard;
    this.apps_resource = i18n_apps;
    if (this.cache.exists('languageCode')) {
      this.languageCode = this.cache.get('languageCode');
    }
  }

  changeAction(event, type) {
    const actionWrapDom = this.element.querySelector(
      '.allapp-search-down-action'
    );
    const curActiveDom = actionWrapDom.querySelector('.active') as HTMLElement;
    if (curActiveDom) {
      this.renderer.removeClass(curActiveDom, 'active');
    }
    if (type === this.actionType) {
      this.actionType = '';
      this.closeRuning = false;
      return;
    }
    const targetDom = event.currentTarget;
    this.renderer.addClass(targetDom, 'active');
    this.actionType = type;
    if (type === 'running') {
      this.closeRuning = false;
    }
  }
  reset() {
    // this.closeRuning = false;
    // const actionWrapDom = this.element.querySelector('.allapp-search-down-action');
    // const curActiveDom = actionWrapDom.querySelector('.active') as HTMLElement;
    // if (curActiveDom) {
    //   this.renderer.removeClass(curActiveDom, 'active');
    // }
    this.actionType = '';
    this.appinstall = false;
    this.removeapp = false;
    this.removeappHT = false;
    this.removeappTB = false;
    this.removeappGG = false;
    this.removeappBX = false;
  }

  // 向导
  navigateTo(url: string) {
    this.reset();
    this.staticModal.hide();
    this.tempAppClick.emit('');
    this.router.navigateByUrl(url);
  }
  // 处理AppGroupComponent下app的Click
  handleWidgetClick(appId: any) {
    this.reset();
    this.staticModal.hide();
    this.tempAppClick.emit('');
  }
  handleAppClick(id: any) {
    this.reset();
    this.staticModal.hide();
    this.appClick.emit(id);
  }

  handleModuleClick(id: any) {
    this.reset();
    this.staticModal.hide();
    this.moduleClick.emit(id);
  }
}



