import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule, Compiler } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { Router, NavigationStart } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { filter } from 'rxjs/operators';
// import { BsDropdownModule, TabsModule, ModalModule, CollapseModule } from 'ngx-bootstrap';

// components
import { AppComponent } from './app.component';
import { FooterComponent } from './component/footer/footer.component';
import { LayoutComponent } from './component/layout/layout.component';
import { WorkAreaComponent } from './component/workarea/workarea.component';
import { HeaderComponent } from './component/header/header.component';
import { TopnavbarComponent } from './component/topnavbar/topnavbar.component';
import { UserInfoMenuComponent } from './component/userinfomenu/userinfomenu.component';
import { MessageNotificationComponent } from './component/messagenotification/messagenotification.component';
import { SystemSettingsComponent } from './component/systemsettings/systemsettings.component';
import { SystemHelpComponent } from './component/systemhelp/systemhelp.component';
import { CreateMenuComponent } from './component/createmenu/createmenu.component';
import { CollectionMenuComponent } from './component/collectionmenu/collectionmenu.component';
import { PageheaderComponent } from './component/pageheader/pageheader.component';
import { ApplicationManagerComponent } from './component/applicationmanager/applicationmanager.component';
import { LayoutContainerComponent } from './containers/layout/layout.container.component';
import { AppGroupComponent } from './component/app-group/app-group.component';
import { AppInfoWidgetComponent } from './component/app-info-widget/app-info-widget.component';

// root route
import { AppRoutingModule } from './app.routing.module';

// services
import { AuthService } from './services/authservice/auth.service';
import { FuncsService } from '../app/rtf-common';
import { HttpService } from '../app/rtf-common';
import { FrameworkService } from '../app/rtf-common';
import { DrawerService } from '../app/rtf-common';
import { FunctionResolver } from '../app/rtf-common';
import { FrmEventBus } from '../app/rtf-common';


// ng2-cache
import {
  CacheService,
  CacheStorageAbstract,
  CacheMemoryStorage
} from '../app/rtf-common';
import { ModalModule } from './component/common/modal';
import { CollapseModule } from './component/common/collapse';
import { BsDropdownModule } from './component/common/dropdown';

// 抽屉
import { GeneralDrawerComponent } from './component/common/general-drawer/general-drawer.component';
// import { GeneralDrawerRelatedComponent } from './component/common/general-drawer-related/general-drawer-related.component';
import { DrawerModule } from '@farris/drawer';

declare var lazyRequire: any;
@NgModule({
  declarations: [
    AppComponent,
    FooterComponent,
    TopnavbarComponent,
    LayoutComponent,
    HeaderComponent,
    UserInfoMenuComponent,
    MessageNotificationComponent,
    SystemSettingsComponent,
    SystemHelpComponent,
    CreateMenuComponent,
    CollectionMenuComponent,
    PageheaderComponent,
    ApplicationManagerComponent,
    WorkAreaComponent,
    LayoutContainerComponent,
    AppGroupComponent,
    AppInfoWidgetComponent,
    GeneralDrawerComponent
  ],
  imports: [
    // Angular modules
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    HttpModule,
    HttpClientModule,
    AppRoutingModule,
    // BsDropdownModule.forRoot(),
    // TabsModule.forRoot(),
    ModalModule.forRoot(),
    CollapseModule.forRoot(),
    BsDropdownModule.forRoot(),
    DrawerModule.forRoot()
  ],
  providers: [
    CacheService,
    AuthService,
    FuncsService,
    HttpService,
    DrawerService,
    FrameworkService,
    FunctionResolver,
    FrmEventBus
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
  //   private readonly routerMap: Map<string, string> = new Map<string, string>([
  //   [ '/main/contract', 'contract.module#./src/app/submodule/contract-pages/contract-pages.module.ts#ContractPagesModule']
  // ]);
  findModuleUrl: string;
  findUrl: string;
  curFuncid: string;
  constructor(private router: Router, private compiler: Compiler, private frmservice: FrameworkService, private cache: CacheService) {
    // 截获路由事件
    router.events
      .pipe(filter(event => event instanceof NavigationStart))
      .subscribe((event: NavigationStart) => {
        // 读取当前路由Url地址
        const routerUrl = event.url;

        // 判断是否拦截当前路由
        if (
          window.hasOwnProperty('funcsRouterMap') &&
          this.matchUrl(routerUrl)
        ) {
          // 读取异步路由资源模块url地址
          const moduleUrl = this.findModuleUrl;
          // 截取导航路径，导航路径为url地址去掉第一个字符'/'
          const navigationPath = this.findUrl.substr(6, routerUrl.length - 1);
          // 读取顶级路由配置对象集合
          const routerConfigs = this.router.config;
          const mainRouterConfigs = this.router.config[1]['children'];

          // 如果未配置此url地址路由，则创建异步路由配置，并记录到路由配置集合
          if (
            !mainRouterConfigs.find(
              routerConfig => routerConfig.path === navigationPath
            )
          ) {
            mainRouterConfigs.push({
              path: navigationPath + '/:funcid',
              // 定义异步路由方法，调用loadModule加载异步路由模块。
              loadChildren: () => this.loadModule(moduleUrl),
              resolve: {
                funcParam: FunctionResolver
              }
            });
            // 重置路由配置
            this.router.resetConfig(routerConfigs);
          }
        }
      });
  }
  matchUrl(url: string): boolean {
    let hasUrl = false;
    const len = window['funcsRouterMap'].length;
    url = url.substr(0, url.lastIndexOf('/'));
    for (let i = 0; i < len; i++) {
      const path = window['funcsRouterMap'][i]['url'];
      if (path === url) {
        hasUrl = true;
        this.findUrl = window['funcsRouterMap'][i]['url'];
        this.findModuleUrl = window['funcsRouterMap'][i]['path'];
        this.curFuncid = window['funcsRouterMap'][i]['fid'];
        break;
      }
    }
    return hasUrl;
  }
  /**
   * 加载异步路由模块
   * @param url 异步路由资源模块url地址
   */
  loadModule(url: string) {
    // 截取异步路由资源模块url地址，获取模块加载信息。
    // tslint:disable-next-line:prefer-const
    let [moduleName, moduleKey, exportName] = url.split('#');
    // 如果未指定导出模块，默认使用default。
    if (exportName === undefined) {
      exportName = 'default';
    }
    // 异步加载资源模块
    return lazyRequire(url)
      .then((module: any) => {
        return module[exportName];
      })
      .then((type: any) => checkNotEmpty(type, moduleName, exportName));
    // .then((type: any) => this.compiler.compileModuleAsync(type));
  }
}
function checkNotEmpty(
  value: any,
  modulePath: string,
  exportName: string
): any {
  if (!value) {
    throw new Error(`Cannot find '${exportName}' in '${modulePath}'`);
  }
  return value;
}



