import {
  Component,
  Input,
  OnInit,
  Output,
  EventEmitter,
  HostBinding,
  HostListener
} from '@angular/core';
import { Function } from '../../models/function.model';
import { i18n_apps } from '../../models/mock.i18n.model';
import { CacheService } from '../../../app/rtf-common';

@Component({
  selector: 'app-app-info-widget',
  templateUrl: './app-info-widget.component.html',
  styleUrls: ['./app-info-widget.component.css']
})
export class AppInfoWidgetComponent implements OnInit {
  @Input() info: Function;

  @Output() appInfoWidgetClick = new EventEmitter();
  i18n_app: any = i18n_apps;
  languageCode = 'zh-CHS';

  @HostListener('click', ['$event'])
  onClick(event) {
    this.appInfoWidgetClick.emit(this.info['Id']);
  }
  constructor(private cache: CacheService) {
    if (this.cache.exists('languageCode')) {
      this.languageCode = this.cache.get('languageCode');
    }
    // this.i18n = i18n_apps;
  }

  ngOnInit() {}
}



