import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { I18n_frm } from '../../models/rtf.i18n.model';
import { CacheService } from '../../../app/rtf-common';
import { AuthService } from '../../services/authservice/auth.service';


@Component({
  selector: 'app-userinfomenu',
  templateUrl: './userinfomenu.component.html',
  styleUrls: ['./userinfomenu.component.css'],
  host: {
    'class': 'gw-userinfo-menu'
  }
})
export class UserInfoMenuComponent implements OnInit {

  constructor(private router: Router, private cache: CacheService, private service: AuthService) { }

  private Userinfo_Setting: string;
  private Userinfo_ChangePwd: string;
  private Userinfo_Logout: string;
  private languageCode = 'zh-CHS';
  private I18n: any;

  ngOnInit() {
    this.I18n = I18n_frm;
    if (this.cache.get('languageCode')) {
      this.languageCode = this.cache.get('languageCode');
    }
  }
  navigateTo(url: string) {
    if (url === 'login') {
      const res = window.confirm('是否确定注销？');
      if (res === true) {
        this.service.Logout().subscribe(res => {});
        this.router.navigateByUrl(url);
      }
    } else {
      this.router.navigateByUrl(url);
    }
  }
}



